import React from 'react';
import s from './Post.module.css'

const Post = (props) => {
    return (

        <div className={s.item}>
            <img src='data:image/jpeg;base64,' alt='Фото' />
            {props.message}

            <div>
                <span>like {props.likeCounts}</span>
            </div>
        </div>


    )
};
export default Post;